@import url(https://fonts.googleapis.com/css?family=Abril+Fatface);
@import url(https://fonts.googleapis.com/css?family=Neuton);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Tattoo fonts */
@font-face {
  font-family: 'Always Beside You';
  src: url("/Tattoo-on-the-tounge/static/media/Always Beside You.9a8f7868.otf");
}

@font-face {
  font-family: 'Reditum';
  src: url(/Tattoo-on-the-tounge/static/media/Reditum.124205af.ttf);
}

::-webkit-scrollbar {
  width: 10px; 
}

/*  The draggable scrolling element resizes depending on the size of the scrollable element. */
::-webkit-scrollbar-thumb {
  background-color: #c0bebe;
}
* {
    box-sizing: border-box;
}

#search-container {
    z-index: 100;
    width: 400px;
    margin: 0 auto;
}

#searchbar {
    position: relative;
}

#searchbar input {
    width: 100%;
    padding: 15px;
    height: 50px;
    font-size: 17px;
    border: 0;
    border-radius: 5px;
    background: white;
    color: #777;
    outline: none;
}

#searchbar .search-btn {
    position: absolute;
    right: 8px;
    top: 12px;
    color: #555;
    border-style: none;
    outline: none;
    background: none;
    font-size: 20px;
    cursor: pointer;
}

#search-container Suggestion {
    position: absolute;
    bottom: 50px;
    width: 100%;
}

#suggestions {
    margin: 0;
    padding: 0;
    list-style-type: none;
    color: #444;
    background: white;
    border-radius: 0 0 5px 5px;
}

#suggestion_li button {
    overflow: hidden;
    text-overflow: ellipsis;
}

/* Last li should not have border-bottom */
#suggestion_li:not(:last-of-type) {
    /* width: 100%; */
    border-bottom: 1px solid grey;
    font-size: 18px;
}

#suggestion_li:hover {
    background: rgb(238, 236, 236);
    
}

/* Last li should have border-radius */
#suggestion_li:last-of-type {
    border-radius: 0 0 5px 5px;
}
